.TodoItem {
    background-color: #FAFAFA;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    border-radius: 8px;
    border: 2px solid #000000;
    /* box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15); */
    transition: 0.2s;
}

.TodoItem:hover {
    margin-top: 18px;
    margin-left: -6;
    border-radius: 8px;
    box-shadow: 10px 10px 0px #000000;
}

.TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.TodoItem-p--complete {
    text-decoration: line-through;
    color: #c0c0c0;
}
